<template>
  <div class="hello">
    <swiper :options="swiperOption" class="swiper-wrap" ref="mySwiper">
      <swiper-slide class="slide1">
      </swiper-slide>
      <swiper-slide class="slide2">
      </swiper-slide>
      <swiper-slide class="slide3">
      </swiper-slide>
      <!--  -->
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    const that = this;
    return {
      imgIndex: 1,
      swiperOption: {
        //notNextTicktrueNextTickswiperswiperswipertrue
        notNextTick: true,
        //
        loop: true,
        //slide
        initialSlide: 0,
        //
        autoplay: {
          delay: 4000,
          stopOnLastSlide: false,
          /*  */
          disableOnInteraction: false
        },
        //
        speed: 800,
        //
        direction: "horizontal",
        //
        grabCursor: true,
        on: {
          //
          slideChangeTransitionStart: function() {
            /* realIndexslide */
            that.imgIndex = this.realIndex - 1;
          }
        },
        //
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
          type: "bullets"
        }
      }
    };
  }
};
</script>

<style lang="less">
.hello{
    height: 420px;
    min-width: 1140px;
    position: relative;
    top: 0;
}
.swiper-wrap{
    height: 424px;
    img{
      position: relative;
      height: 436px;
    }
}
.slide1{
  height: 100%;
  width: 100%;
  background: url(./images/1.jpg) no-repeat center top;
  background-size: cover;
}
.slide2{
    height: 100%;
  width: 100%;
  background: url(./images/2.jpg) no-repeat center top;
  background-size: cover;
}
.slide3{
    height: 100%;
  width: 100%;
  background: url(./images/3.jpg) no-repeat center top;
  background-size: cover;
}
</style>
